.contact {
  background-color: #1c1d20;
}
h1 {
  background: linear-gradient(200deg, rgb(141, 0, 0), #efb968);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 40px 0 40px 0;
}
.form-box {
  margin-top: 10px;
  border-bottom: 2px solid #d6d8de;
}
.field {
  padding: 20px;
  width: 100%;
  background: transparent;
  border: none;
  font-weight: 500;
  display: block;
  color: white;
}
.contact-img {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  overflow: hidden;
}
.contact-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
ul {
  list-style: none;
  padding: 0;
}
li {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: 250px;
  border-radius: 60px;
  margin: 10px;
  transition: transform 0.3s ease, background-color 1s ease;
background-color: #0dcaf0;
  color: rgb(12, 11, 11); /* Change text color to white for better contrast */
  text-decoration: none; /* Remove underline from the link */
}

.btn:hover {
  transform: scale(1.09);
  background-color: #198754;
  color: white;
  
}
.socials > li {
  list-style: none;
  margin: 3%;
}
