.top-container {
  position: relative;
  width: 100%;
  height: 90vh;
  overflow: hidden;
  margin-bottom: 40px;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wrapper {
  width: 100%;
  position: relative;
  height: 500px;
  margin-top: 5rem;
  overflow: hidden;
}

@keyframes scrollleft {
  to {
    left: -100%;
  }
}

.item {
  font-weight: 600;
  font-size: 300%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: absolute;
  left: 100%;
  animation-name: scrollleft;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  color: midnightblue;
}

.item1 {
  animation-delay: calc(10s / 7 * (10 - 1) * -1);
}
.item2 {
  animation-delay: calc(10s / 7 * (10 - 2) * -1);
}
.item3 {
  animation-delay: calc(10s / 7 * (10 - 3) * -1);
}
.item4 {
  animation-delay: calc(10s / 7 * (10 - 4) * -1);
}
.item5 {
  animation-delay: calc(10s / 7 * (10 - 5) * -1);
}
.item6 {
  animation-delay: calc(10s / 7 * (10 - 6) * -1);
}
.item7 {
  animation-delay: calc(10s / 7 * (10 - 7) * -1);
}

.top-heading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: midnightblue;
  font-size: 3rem;
  z-index: 2;
}

.home-box {
  align-items: center;
  display: flex;
  width: 100%;
  max-width: 1100px;
  height: 400px;
  border-radius: 30px;
  margin: 0 auto;
  padding: 20px;
  background-color: midnightblue;
  color: white;
}
.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-color: midnightblue;
  color: white;
  cursor: pointer;
  text-decoration: none;
  transition: transform 0.8s ease-in-out, background-color 0.8s ease-in-out;
}

.circle:hover {
  background-color: black;
  transform: scale(1.2);
  color: black;
  color: white;
}
